<template>
    <KebabMenu
        v-if="props.state"
        :leftAuto="!locale.rtl"
        :rightAuto="locale.rtl"
        :secondary="true"
        :disabled="!props.states?.length"
        size="size-s"
        @toggle="data.stateOpened = !data.stateOpened"
    >
        <template v-slot:trigger>
            <Button
                :icon="`workflow-${ props.state.toLowerCase() }`"
                class="size-s tertiary"
                :class="{
                    active: data.stateOpened,
                    invert: !!props.invert
                }"
                :name="t(`state.default.code.${ props.state.toLowerCase() }`)"
                :kebabTrigger="!!props.states?.length"
            />
        </template>
        <template v-slot>
            <ul>
                <li v-for="item in props.states" :key="item" @click="emit('change', item)">
                    <Icon :icon="`workflow-${ item.toLowerCase() }`" />
                    <span>{{ t(`state.default.${props.to ? 'to' : 'code'}.${ item.toLowerCase() }`) }}</span>
                </li>
            </ul>
        </template>
    </KebabMenu>
</template>

<script setup>
    import { reactive } from 'vue';
    import Button from '🔗/components/ui/Button.vue';
    import KebabMenu from '🔗/components/ui/KebabMenu.vue';
    import stores from '@/stores';

    import { useI18n } from 'vue-i18n';

    const locale = stores.locale();
    const { t } = useI18n();
    const emit = defineEmits(['change']);

    const data = reactive({});

    const props = defineProps({
        to: { type: Boolean, default: true },
        invert: { type: Boolean, default: true },
        state: { type: String, default: '' },
        states: { type: Array, default: () => ([]) }
    });
</script>

<style lang="scss" scoped>
</style>
